function Terms() {
  return (
    <>
      <div  className="container container--terms">
        <div id="content" class="content-lift site-content">
          <div class="row">
            <div id="primary" class="col-sm-12">
              <main id="blog" class="site-main blog-main" role="blog">
                <article
                  id="post-44364"
                  class="post-44364 page type-page status-publish hentry"
                >
                  <div class="content-area">
                    <div class="entry-header">
                      <h1 class="entry-title">Polityka prywatnoci</h1>{" "}
                    </div>

                    <div class="entry-content">
                      <p>
                        <span style={{ fontWeight: "400" }}>
                          Polityka prywatnoci serwisu vellutogiorno.pl
                        </span>
                        <hr />
                      </p>
                      <h3>
                        <span style={{ fontWeight: "400" }}>
                          Informacje oglne
                        </span>
                      </h3>
                      <p>
                        <span style={{ fontWeight: "400" }}>
                          Niniejsza Polityka Prywatnoci okrela zasady
                          przetwarzania i ochrony danych osobowych przekazanych
                          przez Uytkownikw w zwizku z korzystaniem przez nich
                          z usug sklepu internetowego vellutogiorno.pl
                        </span>
                      </p>
                      {/* ////punkty */}
                      <ol>
                        <li style={{ fontWeight: "400" }}>
                          <span style={{ fontWeight: "400" }}>
                            Administratorem danych osobowych zawartych w
                            serwisie jest firma Velluto Giorno z siedzib w
                            Skawie
                          </span>
                        </li>
                        <li style={{ fontWeight: "400" }}>
                          <span style={{ fontWeight: "400" }}>
                            W trosce o bezpieczestwo powierzonych nam danych
                            opracowalimy wewntrzne procedury i zalecenia,
                            ktre maj zapobiec udostpnieniu danych osobom
                            nieupowanionym. Kontrolujemy ich wykonywanie i
                            stale sprawdzamy ich zgodno z odpowiednimi aktami
                            prawnymi - ustaw o ochronie danych osobowych,
                            ustaw o wiadczeniu usug drog elektroniczn, a
                            take wszelkiego rodzaju aktach wykonawczych i
                            aktach prawa wsplnotowego.
                          </span>
                        </li>
                        <li style={{ fontWeight: "400" }}>
                          <span style={{ fontWeight: "400" }}>
                            Dane Osobowe przetwarzane s na podstawie zgody
                            wyraanej przez Uytkownika oraz w przypadkach, w
                            ktrych przepisy prawa upowaniaj Administratora do
                            przetwarzania danych osobowych na podstawie
                            przepisw prawa lub w celu realizacji zawartej
                            pomidzy stronami umowy.
                          </span>
                        </li>
                        <li style={{ fontWeight: "400" }}>
                          <span style={{ fontWeight: "400" }}>
                            Serwis realizuje funkcje pozyskiwania informacji o
                            uytkownikach i ich zachowaniach w nastpujcy
                            sposb: oprzez dobrowolnie wprowadzone w
                            formularzach informacje, oprzez dobrowolnie
                            wprowadzone w formularzach informacje,
                          </span>
                        </li>
                        <li style={{ fontWeight: "400" }}>
                          <span style={{ fontWeight: "400" }}>
                            Serwis zbiera informacje dobrowolnie podane przez
                            uytkownika.
                          </span>
                        </li>
                        <li style={{ fontWeight: "400" }}>
                          <span style={{ fontWeight: "400" }}>
                            Dane podane w formularzu s przetwarzane w celu
                            wynikajcym z funkcji konkretnego formularza np. w
                            celu dokonania procesu obsugi kontaktu
                            informacyjnego
                          </span>
                        </li>
                        <li style={{ fontWeight: "400" }}>
                          <span style={{ fontWeight: "400" }}>
                            Dane osobowe pozostawione w serwisie nie zostan
                            sprzedane ani udostpnione osobom trzecim, zgodnie z
                            przepisami Ustawy o ochronie danych osobowych.
                          </span>
                        </li>
                        <li style={{ fontWeight: "400" }}>
                          <span style={{ fontWeight: "400" }}>
                            Do danych zawartych w formularzu przysuguje wgld
                            osobie fizycznej, ktra je tam umiecia. Osoba ta
                            ma rwnie praw do modyfikacji i zaprzestania
                            przetwarzania swoich danych w dowolnym momencie.
                          </span>
                        </li>
                        <li style={{ fontWeight: "400" }}>
                          <span style={{ fontWeight: "400" }}>
                            Zastrzegamy sobie prawo do zmiany w polityce ochrony
                            prywatnoci serwisu, na ktre moe wpyn rozwj
                            technologii internetowej, ewentualne zmiany prawa w
                            zakresie ochrony danych osobowych oraz rozwj
                            naszego serwisu internetowego. O wszelkich zmianach
                            bdziemy informowa w sposb widoczny i zrozumiay.
                          </span>
                        </li>
                      </ol>
                      <br />
                      <hr />
                      W razie wtpliwoci co ktregokolwiek z zapisw niniejszej
                      polityki prywatnoci jestemy do dyspozycji - nasze dane
                      znale mona w zakadce - KONTAKT.
                      <br /> <br />
                      <hr />
                      <h3>
                        <span style={{ fontWeight: "200" }}>
                          Co to s ciasteczka?
                        </span>
                      </h3>
                      <p>
                        <span style={{ fontWeight: "200" }}>
                          Ciasteczka (ang. cookies) to niewielkie pliki,
                          zapisywane i przechowywane na twoim komputerze,
                          tablecie lub smartphonie podczas gdy odwiedzasz rne
                          strony w internecie. Ciasteczko zazwyczaj zawiera
                          nazw strony internetowej, z ktrej pochodzi, "dugo
                          ycia" ciasteczka (to znaczy czas jego istnienia) oraz
                          przypadkowo wygenerowany unikalny numer sucy do
                          identyfikacji przegldarki, z jakiej nastpuje
                          poczenie ze stron internetow.
                        </span>
                      </p>
                      <br />
                      <h3>
                        <span style={{ fontWeight: "200" }}>
                          Do czego uywamy ciasteczek?
                        </span>
                      </h3>
                      <p>
                        <span style={{ fontWeight: "200" }}>
                          Firma uywa ciasteczek w rnych celach: by strony i
                          aplikacje dziaay szybciej i by byy atwiejsze w
                          uyciu, aby lepiej dopasowa treci i reklamy do
                          twoich oczekiwa i zainteresowa oraz do zbierania
                          anonimowych, zagregowanych statystyk, ktre pomagaj w
                          poprawianiu funkcjonalnoci i zawartoci stron i
                          aplikacji. Uywajc ciasteczek w wyej opisany sposb
                          nigdy nie identyfikujemy tosamoci uytkownikw na
                          podstawie informacji przechowywanych w ciasteczkach.
                        </span>
                      </p>
                    </div>
                  </div>
                </article>
              </main>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

